<template>
    <div class="container">
       <div class="main__todos">
        <div class="main__todos-item" v-for="todo in todos" :key="todo.id">
            <p>{{ todo.title }}</p>
        </div>
       </div>
    </div>
</template>

<script>
    export default {
        created() {
            this.getTodos()
        },
        computed: {
            todos() {
                return this.$store.state.posts
            }
        },
        methods: {
            getTodos() {
                this.$store.dispatch('getTodos')
            }
        } 
    }
</script>